.hero {
  padding-top: 160px;
  min-height: 780px;
  background-color: var(--dark-color);

  &__container {
    @include flex-v-center;
    margin: 0 auto;
  }

  &__image {
    transform: translateX(160px) translateY(4px);

    @include high-tablet {
      display: none;
    }
  }

  &__content {
    max-width: 516px;
    width: 100%;
    @include high-tablet {
      max-width: none;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 50px;
    font-weight: 600;
    font-size: 70px;
    line-height: 160%;
    color: var(--light-color);
    text-transform: uppercase;
  }

  &__title-text {
    display: block;
    font-weight: 400;
    font-size: 20px;
    line-height: 160%;
    max-width: 480px;
    text-transform: initial;
  }


  @include high-tablet {
    min-height: auto;
    padding-bottom: 100px;
  }
}
