.btn {
  border-radius: 5px;
  font-weight: 400;
  font-size: 20px;
  line-height: 160%;
  color: var(--light-color);
  background-color: var(--accent-color);
  padding: 24px 44px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  min-width: 260px;

  &--dark {
    background-color: var(--dark-color);
  }
}
