.order {
  padding: 87px 0;
  background-image: url('/img/percent.png');
  background-color: var(--accent-color);
  background-position: bottom center;
  background-size: cover;
  background-repeat: no-repeat;

  &__container {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
  }

  &__left {
    grid-column: 5 span;
    padding-left: 60px;

    @include high-tablet {
      margin-bottom: 30px;
      grid-column: 12 span;
      padding-left: 0;
    }
  }
  &__right {
    display: flex;
    justify-content: flex-end;
    grid-column: 7 span;
    padding-right: 60px;

    @include high-tablet {
      justify-content: flex-start;
      grid-column: 12 span;
      padding-right: 0;
    }
  }
  &__title {
    margin: 0;
    margin-bottom: 20px;
    font-weight: 600;
    font-size: 36px;
    line-height: 130%;
    color: var(--light-color);
  }
  &__descr {
    font-weight: 400;
    font-size: 20px;
    line-height: 130%;
    color: var(--light-color);
  }
  &__form {
    max-width: 500px;
  }
}

