.social {
  display: flex;
  align-items: center;


  &__item {
    &:not(:last-child) {
      margin-right: 10px;
    }
  }

  &__link {
    width: 30px;
    height: 30px;
    display: block;

    svg {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
      fill: var(--dark-color);
    }
  }
}





